import { CartActionType, } from "@/lib/types/cart";
export const STORAGE_KEY = "cart:v1";
export function calcTotalItems(items) {
    return items.reduce((s, i) => s + i.qty, 0);
}
export function loadCart() {
    try {
        if (typeof window === "undefined")
            return { items: [], totalItems: 0 };
        const raw = localStorage.getItem(STORAGE_KEY);
        if (!raw)
            return { items: [], totalItems: 0 };
        const parsed = JSON.parse(raw);
        const items = Array.isArray(parsed.items) ? parsed.items : [];
        return { items, totalItems: calcTotalItems(items) };
    }
    catch (_a) {
        return { items: [], totalItems: 0 };
    }
}
export function saveCart(state) {
    try {
        if (typeof window === "undefined")
            return;
        localStorage.setItem(STORAGE_KEY, JSON.stringify({ items: state.items, totalItems: state.totalItems }));
    }
    catch (_a) { }
}
export function cartReducer(state, action) {
    switch (action.type) {
        case CartActionType.ADD: {
            const existing = state.items.find((i) => i.variantId === action.item.variantId);
            const items = existing
                ? state.items.map((i) => i.variantId === action.item.variantId
                    ? Object.assign(Object.assign({}, i), { qty: i.qty + action.item.qty }) : i)
                : [...state.items, action.item];
            return { items, totalItems: calcTotalItems(items) };
        }
        case CartActionType.REMOVE: {
            const items = state.items.filter((i) => i.variantId !== action.variantId);
            return { items, totalItems: calcTotalItems(items) };
        }
        case CartActionType.UPDATE_QTY: {
            const items = state.items
                .map((i) => i.variantId === action.variantId ? Object.assign(Object.assign({}, i), { qty: action.qty }) : i)
                .filter((i) => i.qty > 0);
            return { items, totalItems: calcTotalItems(items) };
        }
        case CartActionType.CLEAR:
            return { items: [], totalItems: 0 };
        default:
            return state;
    }
}
